html,
body {
  margin: 0;
  padding: 0;
}
a,
button,
input,
label,
select,
canvas {
  touch-action: manipulation;
}
button {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}
button:active,
button:focus {
  outline: none !important;
}
ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
::selection {
  background: #ccb3e6;
}
.page {
  position: relative;
  width: 1200px;
  margin: 3em auto;
  background-color: #f0f0f0;
  box-shadow: 0 0 100px 2px rgba(0, 0, 0, 0.12);
  border: 1px solid rgba(0, 0, 0, 0.03);
  -webkit-font-smoothing: antialiased;
}
.heading {
  text-align: center;
}
.heading__text {
  display: inline-block;
  margin: 0.6em 0 0.5em;
  color: purple;
  font-size: 4rem;
  font-style: italic;
  font-weight: 400;
  font-family: 'Times New Roman';
  cursor: pointer;
}
.slider {
  display: flex;
  width: 250px;
  height: 70px;
  background-color: #f0f0f0;
  overflow: hidden;
  user-select: none;
}
.slider__button {
  width: 70px;
  height: 100%;
  background-color: #dcd7dc;
  border: 0;
  margin: 0;
  outline: 0;
  overflow: hidden;
  cursor: pointer;
}
.slider__items-view {
  flex: 1;
  height: 100%;
  overflow: hidden;
}
.slider__items-wrapper {
  height: 100%;
  white-space: nowrap;
  transition: transform 0.3s cubic-bezier(0.5, 2.2, 0.4, 0.3);
}
.slider__item {
  display: inline-block;
  width: 100%;
  height: 70px;
  line-height: 70px;
  text-align: center;
}
.checkbox {
  width: 70px;
  height: 70px;
  overflow: hidden;
  border: 3px solid purple;
  background-color: #dcd7dc;
  user-select: none;
}
.checkbox__label {
  display: flex;
  flex-direction: row;
  width: 200%;
  height: 100%;
  transition: transform 0.3s cubic-bezier(0.5, 2.2, 0.4, 0.3);
  cursor: pointer;
}
.checkbox__caption {
  width: 50%;
  height: 100%;
}
.checkbox__checkbox {
  display: none;
}
.checkbox__checkbox:checked + .checkbox__label {
  transform: translateX(-50%);
}
.icon {
  display: block;
  font-size: 0.7rem;
  padding-top: 9px;
}
.icon:hover {
  color: purple;
}
.icon:active {
  padding-top: 11px;
}
.icon::before {
  content: '';
  display: block;
  margin: auto;
  width: 3em;
  height: 3em;
  margin-bottom: 0.2em;
}
.icon--timer-slower::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/timer-speed.svg');
  width: 1.5em;
}
.icon--timer-faster::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/timer-speed.svg');
  transform: scaleX(-1);
  width: 2.2em;
}
.icon--timer-on::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/timer-on.svg');
  width: 1.9em;
}
.icon--timer-off::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/timer-off.svg');
  width: 2.0em;
}
.icon--clear::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/clear.svg');
  width: 1.8em;
}
.icon--world-smaller::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/world.svg');
  width: 1.4em;
}
.icon--world-bigger::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/world.svg');
  width: 2.2em;
}
.icon--next::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/next.svg');
  width: 2.2em;
}
.icon--random::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../images/controls/random.svg');
}
.github {
  position: absolute;
  right: 45px;
  top: 45px;
  transition: transform 0.3s;
}
.github:hover {
  transform: translateY(-16px);
}
.github__image {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  display: block;
  text-align: right;
  width: 4em;
  height: 3.5em;
  opacity: 0.05;
  background-image: url('../images/github/github.svg');
  background-size: 2.5em;
  transition: transform 0.3s, opacity 0.3s;
}
.github:hover .github__image {
  opacity: 0.8;
  transform: scale(1.1);
}
.github__button {
  display: inline-block;
  opacity: 0;
  transition: transform 0.3s, opacity 0.3s;
  transform: translateY(0.6em) scale(0.95);
  padding: 3px 10px 1px 10px;
  margin-top: 2px;
  font-size: 13px;
  line-height: 20px;
  font-weight: bold;
  border: 1px solid #d5d5d5;
  border-radius: 3px;
  background-image: linear-gradient(#fcfcfc, #eee);
  color: #333;
  cursor: pointer;
  text-decoration: none;
  user-select: none;
}
.github__button:hover {
  background-image: linear-gradient(#eee, #ddd);
  border-color: #ccc;
}
.github:hover .github__button {
  opacity: 1;
  transform: translateY(0) scale(1);
}
.github__button::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  width: 12px;
  height: 12px;
  vertical-align: top;
  margin-top: 3px;
  margin-right: 4px;
  background-image: url('../images/github/star.svg');
}
.content {
  position: relative;
  width: 1200px;
  height: 600px;
  margin: 0;
  border: 5px solid purple;
  border-left: 0;
  border-right: 0;
  overflow: hidden;
}
.content__canvas {
  display: block;
  cursor: crosshair;
  opacity: 1;
  transition: opacity 0.3s;
  user-select: none;
}
.content--cursors-select .content__canvas {
  opacity: 0.1;
}
.content__cursors-menu {
  opacity: 0;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.1);
  transform: scale(0.8);
  left: 100%;
  transition: transform 0.3s, left 0.3s step-end, opacity 0.3s;
}
.content--cursors-select .content__cursors-menu {
  transition: transform 0.3s, left 0.0s, opacity 0.3s;
  transform: scale(1);
  left: 0;
  opacity: 1;
}
.cursors-menu {
  height: 100%;
  overflow: auto;
}
.cursors-menu__group {
  padding: 0 26px;
}
.cursors-menu__group:first-child {
  padding-top: 1em;
}
.cursors-menu__group:last-child {
  padding-bottom: 1em;
}
.cursors-menu__heading {
  margin-top: 1.0em;
  margin-bottom: 0.3em;
  font-size: 2.5rem;
  font-weight: 400;
  text-align: center;
}
.cursors-menu__heading--brushes::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  width: 0.7em;
  transform: translateY(1px);
  background-image: url('../images/cursors/brushes.svg');
}
.cursors-menu__heading--important-patterns::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  width: 0.7em;
  transform: translateY(1px);
  background-image: url('../images/cursors/important-patterns.svg');
}
.cursors-menu__heading--guns::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  width: 0.7em;
  transform: translateY(2px);
  background-image: url('../images/cursors/guns.svg');
}
.cursors-menu__heading--spaceships::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  width: 0.75em;
  transform: translateY(3px);
  background-image: url('../images/cursors/spaceships.svg');
}
.cursors-menu__heading--methuselahs::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  transform: translateY(1px);
  background-image: url('../images/cursors/methuselahs.svg');
}
.cursors-menu__heading--puffers::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  width: 0.9em;
  transform: translateY(2px);
  background-image: url('../images/cursors/puffers.svg');
}
.cursors-menu__heading--oscilators::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  width: 0.74em;
  transform: translateY(1px);
  background-image: url('../images/cursors/oscilators.svg');
}
.cursors-menu__heading--still-lifes::before {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  width: 0.8em;
  height: 1em;
  margin-right: 0.3em;
  background-image: url('../images/cursors/still-lifes.svg');
}
.cursors-menu__cursor-wrapper {
  display: inline-block;
  cursor: pointer;
  margin-bottom: 6px;
  margin-left: 3px;
  margin-right: 3px;
}
.cursors-menu__cursor-wrapper:hover {
  color: purple;
}
.cursors-menu__cursor-wrapper:active {
  transform: translateY(2px);
}
.cursors-menu__cursor {
  display: block;
  background-color: rgba(0, 0, 0, 0.05);
  user-select: none;
}
.cursors-menu__cursor-caption {
  text-align: center;
  margin: 0;
  padding: 0.5em 1em;
  background-color: rgba(0, 0, 0, 0.02);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.stats {
  display: none;
  position: absolute;
  left: 1.9em;
  top: 1.75em;
  width: 12em;
}
.stats__table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
}
.stats__separator {
  height: 10px;
}
.stats__heading {
  text-align: left;
  width: 6.4em;
}
.stats__data {
  text-align: right;
}
.controls {
  width: 100%;
  padding: 1.2em 0;
  text-align: center;
}
.controls__item {
  display: inline-block;
  vertical-align: top;
  border: 3px solid purple;
  border-radius: 5px;
}
.controls__item:not(:first-child) {
  margin-left: 1em;
}
.controls__item--wide {
  width: 250px;
  height: 70px;
}
.controls__item--button {
  box-sizing: content-box;
  width: 70px;
  height: 70px;
  background-color: #dcd7dc;
  cursor: pointer;
  user-select: none;
}
.controls__canvas {
  cursor: pointer;
}
.controls__canvas:active {
  transform: translateY(2px);
}
